import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Pagination from "../../../components/Pagination";
import Create from "./child/Create";
import Edit from "./child/Edit";
import Delete from "./child/Delete";
import ModalUID from "../../../components/ModalUID";

export default function Course() {
  const [state, setState] = useState({
    modal: {
      componentAction: null,
      title: "",
      method: null
    }
  });

  var { modal } = state;
  return (
    <div className="container-fluid" style={{}}>

      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">
          Nhóm bài học
        </h4>{" "}
        <div style={{ display: "flex" }}>

          <button
                      style={{ marginRight: "10px" }}

            class={`btn btn-danger btn-icon-split btn-sm  `}
          >
            <span class="icon text-white-50">
              <i class="fas fa-file-export"></i>
            </span>
            <span style={{ color: "white" }} class="text">
              Export Excel
            </span>
          </button>
          <button
            style={{ marginRight: "10px" }}
            class={`btn btn-primary btn-icon-split btn-sm  `}
          >
            <span class="icon text-white-50">
              <i class="fas fa-file-import"></i>
            </span>
            <span style={{ color: "white" }} class="text">
              Import Excel
            </span>
          </button>
          <input
            id="file-excel-import"
            type="file"
            name="name"
            style={{ display: "none" }}
          />
          <button
            className="btn btn-info btn-icon-split btn-sm show"
            onClick={() =>
              setState({ ...state, modal: { componentAction: <Create></Create>, title: "Thêm nhóm bài học", method: "CREATE" } })
            }
            data-toggle="modal"
            data-target="#modalUID"        >
            <span className="icon text-white-50">
              <i className="fas fa-plus" />
            </span>
            <span className="text">Thêm nhóm bài học</span>
          </button>
        </div>

      </div>
      <div className="card shadow ">
        <div className="card-header">
          <div className="row" style={{ justifyContent: "space-between" }}>
            <form>
              <div className="input-group mb-6" style={{ padding: "0px 20px" }}>
                <input
                  type="search"
                  name="txtSearch"
                  className="form-control"
                  placeholder="Tìm nhóm bài học"
                  
                  style={{ maxWidth: "400px", minWidth: "300px" }}
                />
                <div className="input-group-append">
                  <button className="btn btn-primary" type="submit">
                    <i className="fa fa-search" />
                  </button>
                </div>
              </div>
            </form>
       
          </div>
        </div>
        <div className="card-body">
          <div className="table-responsive">
            <table
              className="table table-border "
              id="dataTable"
              width="100%"
              cellSpacing={0}
            >
              <thead>
                <tr>
                  <th>STT</th>
                  <th>Tên nhóm </th>
                  <th>Loại nhóm</th>
                  <th>Người phụ trách</th>
                  <th>SL bài học</th>
                  <th>Tình trạng hoàn thành</th>

                  <th>Hành động</th>

                </tr>
              </thead>
              <tbody>
                <tr className="hover-product">
                  <td>1</td>
                  <td style={{ maxWidth: "200px" }}>
                    <div>
                      <span>Bài học A</span>
                      <div
                        style={{
                          color: "blue", textDecoration: "underline", "font-style": "oblique",
                          "font-size": "13px"
                        }}
                      >
                        <Link to="/train/lesson_question">
                          Xem DS bài học</Link>
                      </div>
                      {/* <div
                        style={{
                          color: "blue", textDecoration: "underline", "font-style": "oblique",
                          "font-size": "13px"
                        }}
                      >
                        <Link to="/course/student">
                          Xem DS học viên</Link>
                      </div> */}
                    </div>
                  </td>
                  <td >Hội nhập</td>
                  <td >Nguyễn Văn A</td>
                  <td >8 bài</td>
                  <td style={{ color: "green" }}>6/8 (bài)</td>



                  <td style={{ maxWidth: "150px", display: "flex" }}>
                    <button
                      name="action"
                      className="btn btn-warning btn-sm show"
                      onClick={() =>
                        setState({ ...state, modal: { componentAction: <Edit></Edit>, title: "Sửa nhóm bài học", method: "EDIT" } })
                      }
                      data-toggle="modal"
                      data-target="#modalUID"                     >
                      <i className="fa fa-edit" /> Sửa
                    </button>
                    <button
                      name="action"
                      onClick={() =>
                        setState({ ...state, modal: { componentAction: <Delete></Delete>, title: "Xóa nhóm bài học", method: "REMOVE" } })
                      }
                      data-toggle="modal"
                      data-target="#modalUID"
                      className="btn btn-danger btn-sm show"
                      style={{ marginLeft: "10px" }}
                    >
                      <i className="fa fa-trash" /> Xóa
                    </button>
                  </td>
                </tr>

              </tbody>
            </table>
          </div>
          <Pagination></Pagination>
        </div>
      </div>
      <ModalUID method={modal.method} title={modal.title}> {modal.componentAction} </ModalUID>

    </div>
  );
}
