import React, { useEffect, useState } from "react";

export default function ModalCreate() {
  const [state, setState] = useState({
    name: "",
  });

  const { name } = state;


  function onChange(e) {
    var { name, value } = e.target;
    setState({ ...state, [name]: value });
  }

  return (
    <form role="form" >
      <div class="form-group">
        <label for="product_name">Tên nhóm</label>
        <input
          type="text"
          class="form-control"
          id="txtName_branch"
          placeholder="Nhập..."
          autocomplete="off"
        />
      </div>
      <div class="form-group">
        <label for="product_name">Loại nhóm</label>
        <select name="name"
          value={name}
          onChange={onChange}
          type="text"
          class="form-control"
          id="txtPhone_branch">
          <option value="">Nhóm A</option>
          <option value="">Nhóm A</option>

        </select>        </div>
      <div class="form-group">
        <label for="product_name">Người phụ trách</label>

        <select name="name"
          value={name}
          onChange={onChange}
          type="text"
          class="form-control"
          id="txtPhone_branch">
          <option value="">Nguyễn Văn A</option>
          <option value="">Nguyễn Văn A</option>

        </select>
      </div>
      <div class="form-group">
        <label for="product_name">Phòng ban</label>
        <select name="name"
          value={name}
          onChange={onChange}
          type="text"
          class="form-control"
          id="txtPhone_branch">
          <option value="">Phòng A</option>
          <option value="">Phòng A</option>

        </select>
      </div>
    </form>
  );
}
